import React, {useState, useEffect} from "react";
import {
	Box,
	Divider,
	FormControl,
	FormControlLabel,
	Grid,
	IconButton,
	makeStyles,
	Paper,
	Radio,
	RadioGroup,
	Typography,
} from "@material-ui/core";
import MasTooltip from "../../components/ui/MasTooltip";
import clsx from "clsx";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import {MasInput} from "../../components/ui/mas-input";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {MasSelect} from "../../components/ui/MasSelect";
import MasDatePicker from "../../components/ui/MasDatePicker";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {CancelledBillRequest} from "../../client/cancel-bills/grpc";
import {useParams} from "react-router";
import {CANCELLED_REQUEST} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {timeStampToDate} from "../../client/converter";
import {MasButton} from "../../components/ui/MasButton";
import CancelBillsTable from "./table";
import {IColumnProps} from "devextreme-react/data-grid";

interface Params {
	formNo: string;
}
export default function CancellBillsAction() {
	const classes = useStyles();
	const [value, setValue] = React.useState("billList");
	const [RequestData, setRequestData] = useState<CANCELLED_REQUEST.AsObject>({} as any);
	let {formNo} = useParams<Params>();

	const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		setValue((event.target as HTMLInputElement).value);
	};

	const commentTemplete = (rowData: any) => {
		return (
			<>
				<MasTooltip title=" ">
					<IconButton aria-label="Edit" className={classes.circle}>
						<LinkRoundedIcon />
					</IconButton>
				</MasTooltip>
			</>
		);
	};

	useEffect(() => {
		CancelledBillRequest(Number(formNo))
			.then((data) => {
				setRequestData(data);
				console.log(data);
			})
			.catch((er) => {
				console.log(er);
			});
	}, [formNo]);

	const billsListColumns: IColumnProps[] = [
		{
			caption: " ",
			dataField: "paymentNo",
			alignment: "center",
		},
		{
			caption: " ",
			dataField: "bilngDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.bilngDate);
				return date.toLocaleDateString();
			},
		},
		{caption: " ", dataField: "clBlnce"},
		{caption: "", dataField: "comment"},
		{
			caption: "",
			dataField: "",
			cellRender: (row) => commentTemplete(row.data.formNo),
			type: "buttons",
		},
	];
	const actionsListColumns: IColumnProps[] = [
		{
			caption: " ",
			dataField: "stampDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.stampDate);
				return date.toLocaleDateString();
			},
		},
		{caption: " ", dataField: "description"},
		{caption: "", dataField: "stampUser"},
		{caption: "", dataField: "comment"},
	];
	return (
		<MuiPickersUtilsProvider utils={DateFnsUtils}>
			<Box className="page-wrapper">
				<MasPageHeader title="  " />
				<Grid spacing={2} container justify="center">
					<Grid item xs={4}>
						<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
							<Box display="flex" justifyContent="space-between" alignItems="center">
								<Typography
									component="span"
									variant="h6"
									className={classes.formTitle}
									color="initial"
								>
									  {RequestData.formNo}
								</Typography>
								<Typography
									className={classes.formTitle}
									component="span"
									variant="subtitle1"
									color="secondary"
								>
									{RequestData.status || ""}
								</Typography>
							</Box>
							<Box mt={1} mb={1}>
								<Divider variant="fullWidth" />
							</Box>
							<Box mb={1}>
								<MasSelect
									items={Items}
									itemText="name"
									itemValue="name"
									fullWidth
									inputProps={{
										id: "filled-age-native-simple",
									}}
									label=""
								/>
							</Box>
							<Box mb={1}>
								<MasInput
									label="  "
									multiline={true}
									rowsMax={4}
									rows={2}
								/>
							</Box>
							<Box textAlign="center" mx="auto" mb={0.5}>
								<MasButton
									disableElevation
									label=""
									variant="contained"
									color="primary"
								/>
							</Box>
						</Paper>
						<Box mt={2}>
							<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
								<Box display="flex" justifyContent="space-between" alignItems="center">
									<Typography
										component="span"
										variant="h6"
										className={classes.formTitle}
										color="initial"
									>
										 
									</Typography>
								</Box>
								<Box mt={1} mb={1}>
									<Divider variant="fullWidth" />
								</Box>
								<Box mb={1}>
									<MasInput label=" " disabled value={RequestData?.surname} />
								</Box>
								<Box mb={1}>
									<MasInput
										label=" "
										type="number"
										value={RequestData?.custkey}
										disabled
									/>
								</Box>
								<Box mb={1}>
									<MasInput
										label=" "
										type="number"
										value={RequestData?.documentNo}
										disabled
									/>
								</Box>
								<Box mb={1}>
									<MasDatePicker
										label=" "
										disabled
										value={timeStampToDate(RequestData.requestDate)}
										onChange={() => {}}
									/>
								</Box>
								<Box mb={1}>
									<MasInput
										label="  "
										multiline={true}
										rowsMin={2}
										rowsMax={4}
										rows={2}
										labelWidth={110}
										value={RequestData.comment}
										disabled
									/>
								</Box>
							</Paper>
						</Box>
					</Grid>
					<Grid item xs={8}>
						<Paper className={clsx(classes.paper, classes.table)} elevation={0}>
							<Box pt={1} pb={1}>
								<FormControl component="fieldset">
									<RadioGroup
										onChange={handleChange}
										row
										aria-label="position"
										name="position"
										value={value}
									>
										<FormControlLabel
											value="billList"
											control={<Radio color="secondary" />}
											label=" "
											labelPlacement="start"
										/>
										<FormControlLabel
											value="actionsList"
											control={<Radio color="secondary" />}
											label=" "
											labelPlacement="start"
										/>
									</RadioGroup>
								</FormControl>
							</Box>
							{value === "billList" ? (
								<CancelBillsTable
									keyExpr="paymentNo"
									rows={RequestData.billsList}
									columns={billsListColumns}
								/>
							) : (
								<CancelBillsTable
									keyExpr="actionId"
									rows={RequestData.actionsList}
									columns={actionsListColumns}
								/>
							)}
						</Paper>
					</Grid>
				</Grid>
			</Box>
		</MuiPickersUtilsProvider>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paper: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[4],
		boxShadow: theme.m_shadows[1],
	},
	form: {
		padding: theme.spacing(1.5, 2),
	},
	formTitle: {
		fontFamily: "DINMED",
		padding: theme.spacing(0, 0.5),
	},
	table: {
		padding: theme.spacing(1.5),
	},
	styleFormControl: {
		width: "auto",
		flexGrow: 1,
	},
	loadCutomerBill: {
		paddingTop: 13,
		paddingBottom: 13,
	},
	circle: {
		margin: theme.spacing(0, 1),
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		position: "relative",
		top: "4px",
	},
	selectionInfo: {
		padding: "8px",
		textAlign: "right",
		color: theme.palette.grey[600],
	},
	input: {
		width: "100%",
	},
}));

const Items = [
	{id: "1", name: " "},
	{id: "2", name: "   "},
	{id: "2", name: "  "},
];
