import React from "react";
import DataGrid, {
	Column,
	IColumnProps,
	HeaderFilter,
	SearchPanel,
	Selection,
	Editing,
	IDataGridOptions,
} from "devextreme-react/data-grid";
import Button from "devextreme-react/button";
import {FontBase64} from "../../components/fontBase64";
import {jsPDF} from "jspdf";
import "jspdf-autotable";
import {exportDataGrid as exportDataGridToPdf} from "devextreme/pdf_exporter";
import {timeStampToDate} from "../../client/converter";
interface TableProps extends IDataGridOptions {
	rows: Array<any>;
	keyExpr: string;
	columns: Array<IColumnProps>;
	printPdf?: boolean;
	_selection?: boolean;
	_editing?: boolean;
}

function CancelBillsTable(props: TableProps) {
	const {rows, keyExpr, columns, _selection, _editing, printPdf, onSelectionChanged} =
		props;
	const dataGridRef = React.useRef(null);

	function exportGrid() {
		const doc = new jsPDF("l");
		doc.addFileToVFS("Cairo", FontBase64);
		doc.addFont("Cairo", "Cairo", "normal");
		doc.setFont("Cairo"); // set font
		const dataGrid: any = dataGridRef.current.instance;
		exportDataGridToPdf({
			jsPDFDocument: doc,
			component: dataGrid,
			autoTableOptions: {
				theme: "striped",
				willDrawCell: (data) => {
					if (data.section === "head") {
						if (data?.cell?.raw?.content === undefined) {
							data.cell.text = columns[data.column.index].caption;
						}
					}
				},
				didParseCell: (data) => {},
				didDrawPage: (data) => {
					// console.log(data.table.head[0].raw, "didDrawPage");
				},
			},
			customizeCell: function (options) {
				const {gridCell, pdfCell} = options;
				pdfCell.alternateRowStyles = {
					fillColor: [74, 96, 117],
				};
				if (gridCell.rowType === "header") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 12,
						halign: "center",
						valign: "middle",
						textColor: 255,
					};
				}
				if (gridCell.rowType === "data") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 12,
						halign: "center",
					};
				}
				if (gridCell.column.dataType === "date") {
					let dd = timeStampToDate(gridCell?.data?.requestDate);
					pdfCell.content = dd?.toLocaleDateString();
				}
			},
		}).then(() => {
			doc.save("table.pdf");
		});
	}

	return (
		<>
			{printPdf && (
				<Button id="exportButton" icon="exportpdf" text="" onClick={exportGrid} />
			)}
			<DataGrid
				showColumnLines
				ref={dataGridRef}
				rowAlternationEnabled
				dataSource={rows}
				keyExpr={keyExpr}
				showBorders={true}
				onSelectionChanged={onSelectionChanged}
			>
				<HeaderFilter visible />
				<SearchPanel visible />
				{_selection && <Selection showCheckBoxesMode="always" mode="multiple" />}
				{_editing && <Editing mode="cell" allowUpdating={true} />}
				{columns.map((col, ind) => {
					if (col.cellRender) {
						return (
							<Column
								alignment="center"
								key={ind}
								cellRender={col.cellRender}
								dataField={col.dataField}
								dataType={col.dataType}
								caption={col.caption}
								width={col.width}
								type={col.type}
								allowEditing={col.allowEditing}
								editCellComponent={col.editCellComponent}
								editCellRender={col.editCellRender}
							/>
						);
					}
					return (
						<Column
							alignment="center"
							dataField={col.dataField}
							key={ind}
							caption={col.caption}
							width={col.width}
							allowEditing={col.allowEditing}
							editCellComponent={col.editCellComponent}
							editCellRender={col.editCellRender}
						/>
					);
				})}
			</DataGrid>
		</>
	);
}

export default React.memo(CancelBillsTable);
