import "date-fns";
import React from "react";
import clsx from "clsx";
import {makeStyles, TextFieldProps} from "@material-ui/core";

import {KeyboardDatePicker, DatePickerProps} from "@material-ui/pickers";
import {MasTextField} from "./MasTextField";

interface DateProps extends DatePickerProps {
	styleDate?: any;
}

const useStyles = makeStyles((theme) => ({
	root: {},
}));

const TextFieldComponent = (props: TextFieldProps) => {
	return <MasTextField {...props} />;
};
export default function MasDatePicker(props: DateProps) {
	const {styleDate, ...restProps} = props;
	const classes = useStyles();
	return (
		<KeyboardDatePicker
			className={clsx(classes.root, [styleDate])}
			disableFuture
			autoOk
			openTo="year"
			views={["year", "month", "date"]}
			fullWidth
			variant="inline"
			format="yyyy/MM/dd"
			inputVariant="filled"
			TextFieldComponent={TextFieldComponent}
			{...restProps}
		/>
	);
}
