import {createMuiTheme} from "@material-ui/core/styles";

const primary = "#007D7D", // 006666
	secondary = "#FF836B",
	third = "#FFBC00",
	primaryLight = "#DDF3F3",
	secondaryLight = "#FFCFC6",
	primaryDrak = "#006666",
	white = "#ffffff",
	background = "#002e43",
	textBlack = "#191B1B",
	textLight = "#889797",
	icon = "#758B8B",
	iconLihgt = "#b9d9e8",
	//grey0 = "#FAFAFA",
	//grey1 = "#F2F2F2",
	grey2 = "#EAEAEA",
	grey3 = "#D7D7DE",
	grey4 = "#BFC0C7",
	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	grey5 = "#ABABB4";

declare module "@material-ui/core/styles/createPalette" {
	interface Palette {
		third: Palette["primary"];
		icon: Palette["primary"];
		greyscale: object;
	}
	interface PaletteOptions {
		third: PaletteOptions["primary"];
		icon: PaletteOptions["primary"];
		greyscale: object;
	}
}

declare module "@material-ui/core/styles/createMuiTheme" {
	interface Theme {
		m_shadows: Array<React.CSSProperties["boxShadow"]>;
	}
	interface ThemeOptions {
		m_shadows: Array<React.CSSProperties["boxShadow"]>;
	}
}

declare module "@material-ui/core/styles/createMuiTheme" {
	interface Theme {
		gradients: Array<React.CSSProperties["background"]>;
	}
	interface ThemeOptions {
		gradients: Array<React.CSSProperties["background"]>;
	}
}
declare module "@material-ui/core/styles/createMuiTheme" {
	interface Theme {
		border: Array<React.CSSProperties["border"]>;
	}
	interface ThemeOptions {
		border: Array<React.CSSProperties["border"]>;
	}
}
declare module "@material-ui/core/styles/createMuiTheme" {
	interface Theme {
		raduis: Array<React.CSSProperties["borderRadius"]>;
	}
	interface ThemeOptions {
		raduis: Array<React.CSSProperties["borderRadius"]>;
	}
}

const theme = createMuiTheme({
	palette: {
		primary: {
			main: primary,
			contrastText: white,
			light: primaryLight,
			dark: primaryDrak,
		},
		secondary: {
			main: secondary,
			contrastText: white,
			light: secondaryLight,
		},
		text: {
			primary: textBlack,
			secondary: textLight,
		},
		background: {
			default: background,
		},
		third: {
			main: third,
			contrastText: textBlack,
		},
		icon: {
			main: icon,
			light: iconLihgt,
		},
		greyscale: {
			1: "#FAFAFA",
			2: "#F2F2F2",
			3: "#EAEAEA",
			4: "#E3E3E3",
			5: "#D7D7DE",
			6: "#BFC0C7",
			7: "#B7B7BE",
			8: "#ABABB4",
			9: "#9E9FAB",
			10: "#9596A4",
			11: "#858696",
			12: "#7F7F8D",
			13: "#757583",
			14: "#6B6C7B",
			15: "#5C5D6F",
			16: "#4E4F60",
			17: "#3F404F",
			18: "#3D3E51",
			19: "#323346",
			20: "#262739",
		},
	},
	m_shadows: [
		"0 1px 12px 0 rgba(46, 61, 73, 0.15)", //0
		"0 1px 20px 0 rgba(46, 61, 73, 0.2)", //1
	],
	gradients: [
		"linear-gradient( 108deg, rgba(1, 245, 220,0.9) 16%, rgb(172, 182, 229, 0.9) 99% )",
		"linear-gradient( 108deg, rgba(77, 255, 220,0.5) 16%, rgb(172, 182, 229, 0.5) 99% )",
	],
	raduis: ["8px", "12px", "16px", "20px", "24px"],
	border: [`1px solid ${grey2}`, `1px solid ${grey3}`, `1px solid ${grey4}`],
	typography: {
		fontFamily: "inherit",
	},
	direction: "rtl",
	overrides: {
		MuiCssBaseline: {
			"@global": {
				html: {
					WebkitFontSmoothing: "auto",
				},
				body: {
					backgroundColor: "#fff",
					fontFamily: "DINREG ,  Helvetica, Arial, sans-serif",
				},
			},
		},
		MuiInputBase: {
			root: {
				fontSize: "1.2rem",
				"@media (min-width: 1700px)": {
					fontSize: "1.2rem",
				},
				"&::before": {
					border: "none",
				},
			},
		},
		MuiFilledInput: {
			root: {
				backgroundColor: "#f3f5f5",
				border: "1px solid #d3dede",
				color: textBlack,
				"&:hover": {
					backgroundColor: "#f4f4f4",
				},
			},
			underline: {
				"&:before": {
					borderBottom: 0,
				},
			},
			input: {
				padding: "20px 8px",
				height: "2rem",
				paddingBottom: "4px",
			},
			multiline: {
				padding: "24px 12px 4px",
			},
		},
		MuiButton: {
			outlinedSecondary: {
				borderColor: "#c6c8ca",
			},
		},
		MuiTab: {
			root: {
				border: "1px solid rgb(0 157 157 / 20%)",
				"&:hover": {
					backgroundColor: "rgb(0 225 235 / 20%)",
				},
				"&.Mui-selected": {
					transition: "opacity .1s ease",
					fontFamily: "DINBLD",
					color: "#fff",
					background: primary,
					borderRadius: "4px",
					border: 0,
				},
			},
			textColorInherit: {
				opacity: 0.6,
			},
		},
		MuiTabs: {
			indicator: {
				display: "flex",
				justifyContent: "center",
				backgroundColor: "transparent",
				bottom: "5px",
				"& > span": {
					maxWidth: 50,
					width: "100%",
				},
			},
		},
		MuiTableRow: {
			root: {
				"&.Mui-selected, Mui-selected:hover": {
					backgroundColor: "rgb(218 223 230) !important",
				},
			},
		},
		MuiCheckbox: {
			root: {
				color: "inherit",
				"&.Mui-checked ": {
					color: "#007eff",
				},
			},
		},
		MuiChip: {
			root: {
				backgroundColor: "#fff",
				border: "1px solid #ddd",
				fontSize: "14px",
				fontWeight: "bold",
				fontFamily: "Helvetica-Bold, sans-serif",
			},
		},
	},
	props: {
		MuiIconButton: {
			//disableRipple: true,
		},
	},
});

export default theme;
