import React from "react";
import {
	createStyles,
	Theme,
	FormControl,
	InputBase,
	withStyles,
	Typography,
	makeStyles,
	InputBaseProps,
	fade,
} from "@material-ui/core";

import {blue} from "@material-ui/core/colors";

interface props extends InputBaseProps {
	styleLabel?: string;
	styleInput?: string;
	styleFormControl?: string;
	label?: string;
	fullWidth?: boolean;
}

const useStyles = makeStyles((theme) => ({
	label: {
		margin: "0 4px 6px 0",
		color: theme.palette.grey[600],
	},
}));

const BootInput = withStyles((theme: Theme) =>
	createStyles({
		root: {
			"label + &": {
				marginTop: theme.spacing(1),
			},
		},
		input: {
			borderRadius: 4,
			position: "relative",
			fontFamily: "DINMED",
			backgroundColor: theme.palette.grey[50],
			border: `1px solid ${theme.palette.grey[300]}`,
			fontSize: "1.1rem",
			width: "100%",
			padding: theme.spacing(1.7, 1.8),
			transition: theme.transitions.create(["border-color", "box-shadow"]),
			"&:focus": {
				boxShadow: `0 0 0 0.2rem ${fade(blue[600], 0.4)}`,
				borderColor: fade(blue[600], 0.4),
			},
		},
	})
)(InputBase);

export function MasInput(props: props) {
	const {styleLabel, styleInput, styleFormControl, fullWidth, label, ...InputProps} =
		props;
	const classes = useStyles();
	return (
		<FormControl
			variant="filled"
			fullWidth={fullWidth || true}
			className={styleFormControl}
		>
			{label && (
				<Typography variant="subtitle1" className={classes.label}>
					{label}
				</Typography>
			)}
			<BootInput className={styleInput} {...InputProps} />
		</FormControl>
	);
}
