import {
	CANCELLED_REQUEST,
	LU_CANCELLED_BILL_STATE,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {
	CancelledBillListRequest,
	CancelledBillListResponse,
	CancelledBillRequestRequest,
	Empty,
	GetCustomerPaymentsRequest,
	SaveBillCancelRequestRequest,
	SaveBillCancelRequestResponse,
} from "../../masProtos/generated/messages/collectionmessages_pb";
import {CollectionDestributionItem} from "../../masProtos/generated/serverhostmessages/collectionmessages_pb";
import * as servicse from "../../masProtos/generated/services/collection_pb_service";
import {hideLoading, showLoading} from "../../redux/store";
import {RequestMeta} from "../grpc-client";
import {FROM_CANCELLED_REQUEST} from "./converter";
let port = 25567;
let collectionClient: servicse.collectionClient;

export function GetCollectionClient(): Promise<servicse.collectionClient> {
	return new Promise(async (resolve, reject) => {
		try {
			let host = window.location.hostname;
			collectionClient = new servicse.collectionClient(`http://${host}:${port}`, {
				debug: false,
			});
			resolve(collectionClient);
		} catch (err) {
			reject(err);
		}
	});
}

export function CancelledBillList(
	state?: number
): Promise<CancelledBillListResponse.AsObject> {
	return new Promise<CancelledBillListResponse.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let req = new CancelledBillListRequest();
			req.setState(state);
			const meta: any = RequestMeta(10000);
			const clinet = await GetCollectionClient();
			clinet.cancelledBillList(req, meta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					resolve(resp.toObject());
					hideLoading();
				}
			});
		} catch (err) {
			reject(err);
		}
	});
}

export function BillStates(): Promise<Array<LU_CANCELLED_BILL_STATE.AsObject>> {
	return new Promise<Array<LU_CANCELLED_BILL_STATE.AsObject>>(async (resolve, reject) => {
		try {
			const meta: any = RequestMeta(10000);
			const req = new Empty();
			const clinet = await GetCollectionClient();
			clinet.billStates(req, meta, (err, resp) => {
				if (err != null) {
					reject(err);
				} else {
					let list = resp.getItemsList().map((a) => a.toObject());
					resolve(list);
				}
			});
		} catch (err) {
			reject(err);
		}
	});
}

export function GetCustomerPayments(
	custkey: string
): Promise<Array<CollectionDestributionItem.AsObject>> {
	return new Promise<Array<CollectionDestributionItem.AsObject>>(
		async (resolve, reject) => {
			try {
				showLoading();
				let req = new GetCustomerPaymentsRequest();
				req.setCustkey(custkey);
				const meta: any = RequestMeta(10000);
				const clinet = await GetCollectionClient();
				clinet.getCustomerPayments(req, meta, (err, resp) => {
					if (err != null) {
						reject(err);
						hideLoading();
					} else {
						let list = resp?.getItemsList().map((a) => a.toObject());
						resolve(list);
						hideLoading();
					}
				});
			} catch (e) {
				reject(e);
			}
		}
	);
}

export function SaveBillCancelRequest(
	_req: CANCELLED_REQUEST.AsObject
): Promise<SaveBillCancelRequestResponse.AsObject> {
	return new Promise<SaveBillCancelRequestResponse.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let cancelReq = FROM_CANCELLED_REQUEST(_req);
			let req = new SaveBillCancelRequestRequest();
			req.setRequest(cancelReq);
			const meta: any = RequestMeta(10000);
			const clinet = await GetCollectionClient();
			clinet.saveBillCancelRequest(req, meta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					resolve(resp.toObject());
					hideLoading();
				}
			});
		} catch (e) {
			reject(e);
		}
	});
}
export function CancelledBillRequest(
	formNo: number
): Promise<CANCELLED_REQUEST.AsObject> {
	return new Promise<CANCELLED_REQUEST.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let req = new CancelledBillRequestRequest();
			req.setFormNo(formNo);
			const meta: any = RequestMeta(10000);
			const clinet = await GetCollectionClient();
			clinet.cancelledBillRequest(req, meta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					let list = resp.getContent().toObject();
					resolve(list);
					hideLoading();
				}
			});
		} catch (e) {
			reject(e);
		}
	});
}
