import React from "react";
import {
	FormControl,
	FilledInput,
	InputLabel,
	OutlinedInputProps,
} from "@material-ui/core";
interface props extends OutlinedInputProps {
	label?: string;
	styleFormControl?: string;
}
export function MasInput(props: props) {
	const {styleFormControl, label, ...InputProps} = props;
	return (
		<FormControl className={styleFormControl} fullWidth variant="filled">
			<InputLabel shrink htmlFor="outlined-adornment-amount">
				{label}
			</InputLabel>
			<FilledInput labelWidth={75} {...InputProps} />
		</FormControl>
	);
}
