import React, {useState, useEffect} from "react";
import {Box, Divider, Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import clsx from "clsx";
import {MasInput} from "../../components/ui/mas-input";
import {MasButton} from "../../components/ui/MasButton";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {UploadFileIcon} from "../../components/ui/mas-icons";
import SaveRoundedIcon from "@material-ui/icons/SaveRounded";
import MasDatePicker from "../../components/ui/MasDatePicker";
import {MaterialUiPickersDate} from "@material-ui/pickers/typings/date";
import DateFnsUtils from "@date-io/date-fns";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import {GetCustomerPayments, SaveBillCancelRequest} from "../../client/cancel-bills/grpc";
import {CollectionDestributionItem} from "../../masProtos/generated/serverhostmessages/collectionmessages_pb";
import {showNotification} from "../../redux/store";
import {IColumnProps} from "devextreme-react/data-grid";
import CancelBillsTable from "./table";
import {dateToTimeStamp, timeStampToDate} from "../../client/converter";
import {
	CANCELLED_BILL,
	CANCELLED_REQUEST,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {EventInfo} from "devextreme/events";
import dxDataGrid, {SelectionChangedInfo} from "devextreme/ui/data_grid";
import {MasTextField} from "../../components/ui/MasTextField";

// type myobj={[key:string]:any}

interface ICancelReqState {
	cancelledRequest: CANCELLED_REQUEST.AsObject;
	cancelledBill: Array<CANCELLED_BILL.AsObject>;
	customerPayments: Array<CollectionDestributionItem.AsObject>;
}

export default function CancellBillsRequest() {
	const classes = useStyles();
	const [dateNow] = useState<MaterialUiPickersDate>(new Date());
	const [state, setState] = useState<ICancelReqState>({} as any);
	useEffect(() => {
		let dateStamp = dateToTimeStamp(dateNow, "").toObject();
		setState({
			...state,
			cancelledRequest: {
				...state.cancelledRequest,
				requestDate: {
					nanos: dateStamp.nanos,
					seconds: dateStamp.seconds,
				},
				stationNo: 10,
				formNo: 0,
				requestBy: "",
				status: "",
				surname: "",
			},
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	const getCustomerPayments = (cKey: string) => {
		if (!state.cancelledRequest.custkey) {
			alert("   ");
			return;
		}
		GetCustomerPayments(cKey)
			.then((payments) => {
				let newArr = payments.map((item) => {
					return {
						...item,
						comment: "",
					};
				});
				setState({
					...state,
					customerPayments: newArr,
				});
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};

	const saveCancelReq = () => {
		console.log(state.cancelledRequest);

		SaveBillCancelRequest(state.cancelledRequest)
			.then((data) => {
				alert(data);
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};

	const handelChangeForm = (event) => {
		const target = event.target;
		const value = target.value;
		const name: any = target.name;
		setState({
			...state,
			cancelledRequest: {
				...state.cancelledRequest,
				[name]: value,
			},
		});
	};

	const handelRowSelected = (event: EventInfo<dxDataGrid> & SelectionChangedInfo) => {
		let data = event.selectedRowsData;
		console.log(data);
		let rows = data.map((item) => {
			return {
				...item,
				formNo: 0,
				documentNo: state.cancelledRequest.documentNo,
				cancelledBy: "",
			};
		});
		setState({
			...state,
			cancelledRequest: {
				...state.cancelledRequest,
				billsList: rows,
			},
		});
	};

	const columns: IColumnProps[] = [
		{
			caption: "  ",
			dataField: "paymentNo",
			alignment: "center",
			allowEditing: false,
		},
		{
			caption: " ",
			dataField: "bilngDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.bilngDate);
				return date.toLocaleDateString();
			},
			allowEditing: false,
		},
		{caption: " ", dataField: "clBlnce", width: 150, allowEditing: false},
		{caption: "", dataField: "curCharges", width: 150, allowEditing: false},
		{
			caption: "",
			dataField: "comment",
			width: 250,
			cellRender: (row) => {
				let val = row.data.comment;
				console.log(val);
				return <MasTextField variant="standard" value={row.data.comment} />;
			},
		},
	];
	return (
		<MuiPickersUtilsProvider utils={DateFnsUtils}>
			<Box className="page-wrapper">
				<MasPageHeader title="  " />
				<Grid spacing={2} container justify="center">
					<Grid item xs={3}>
						<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
							<Typography variant="h6" className={classes.formTitle} color="initial">
								 
							</Typography>
							<Box mt={1} mb={2}>
								<Divider variant="fullWidth" />
							</Box>
							<Box mb={2} display="flex" alignItems="center">
								<MasInput
									styleFormControl={classes.styleFormControl}
									label=" "
									fullWidth={false}
									value={state?.cancelledRequest?.custkey}
									name="custkey"
									onChange={(e) => handelChangeForm(e)}
								/>
								<MasButton
									label=" "
									disableElevation
									color="primary"
									variant="outlined"
									styleBtn={classes.loadCutomerBill}
									endIcon={<UploadFileIcon />}
									onClick={() => getCustomerPayments(state.cancelledRequest.custkey)}
								/>
							</Box>
							<Box mb={2}>
								<MasInput
									label=" "
									type="number"
									name="documentNo"
									onChange={(e) => handelChangeForm(e)}
								/>
							</Box>
							<Box mb={2}>
								<MasDatePicker
									label=" "
									value={dateNow}
									name="requestDate"
									onChange={(data) => {
										let dateStamp = dateToTimeStamp(data, "").toObject();
										setState({
											...state,
											cancelledRequest: {
												...state.cancelledRequest,
												requestDate: {
													nanos: dateStamp.nanos,
													seconds: dateStamp.seconds,
												},
											},
										});
									}}
								/>
							</Box>
							<Box mb={1}>
								<MasInput
									label="  "
									multiline={true}
									rowsMin={2}
									rowsMax={4}
									rows={3}
									labelWidth={110}
									name="comment"
									onChange={(e) => handelChangeForm(e)}
								/>
							</Box>
							<Box mt={3} mb={1} textAlign="center">
								<MasButton
									endIcon={<SaveRoundedIcon />}
									label=" "
									color="secondary"
									variant="contained"
									onClick={saveCancelReq}
									type="submit"
								/>
							</Box>
						</Paper>
					</Grid>
					<Grid item xs={9}>
						<Paper className={clsx(classes.paper, classes.table)} elevation={0}>
							<Box display="flex" alignItems="center" justifyContent="space-between">
								<Typography variant="h6" className={classes.formTitle}>
									 
								</Typography>
							</Box>
							<Box mt={1} mb={2}>
								<Divider variant="fullWidth" />
							</Box>
							<Box className={classes.dataGrid}>
								<CancelBillsTable
									keyExpr="paymentNo"
									columns={columns}
									rows={state?.customerPayments}
									_selection
									onSelectionChanged={(e) => handelRowSelected(e)}
									_editing
								/>
							</Box>
						</Paper>
					</Grid>
				</Grid>
			</Box>
		</MuiPickersUtilsProvider>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paper: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[4],
		boxShadow: theme.m_shadows[1],
	},
	form: {
		padding: theme.spacing(1.5, 2),
	},
	formTitle: {
		fontFamily: "DINMED",
		padding: theme.spacing(0, 0.5),
	},
	table: {
		height: "100%",
		padding: theme.spacing(1.5, 2),
	},
	styleFormControl: {
		width: "auto",
		flexGrow: 1,
	},
	loadCutomerBill: {
		paddingTop: 13,
		paddingBottom: 13,
	},
	circle: {
		margin: theme.spacing(0, 1),
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		position: "relative",
		top: "4px",
	},
	selectionInfo: {
		padding: "8px",
		textAlign: "right",
		color: theme.palette.grey[600],
	},
	input: {
		width: "100%",
	},
	dataGrid: {
		border: theme.border[1],
		borderRadius: theme.raduis[1],
		padding: theme.spacing(1),
	},
}));
