import React, {useState} from "react";
import {
	Box,
	fade,
	Grid,
	IconButton,
	makeStyles,
	Paper,
	Typography,
} from "@material-ui/core";
import {MasButton} from "../../components/ui/MasButton";
import {Link} from "react-router-dom";
import CreateRoundedIcon from "@material-ui/icons/CreateRounded";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import clsx from "clsx";
import MasTooltip from "../../components/ui/MasTooltip";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {MasList} from "../../components/ui/MasList";
import {AddCircleOutline, PollRounded} from "@material-ui/icons";
import CancelBillsTable from "./table";
import {IColumnProps} from "devextreme-react/tree-list";
import {
	BillStates,
	CancelledBillList,
} from "../../client/cancel-bills/grpc";
import {timeStampToDate} from "../../client/converter";
import {
	CANCELLED_REQUEST,
	LU_CANCELLED_BILL_STATE,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {showNotification} from "../../redux/store";

interface Istate {
	listItemId?: number;
	selectListItem?: boolean;
}

export default function CancellBills() {
	const classes = useStyles();

	const [cancelledBills, setCancelledBills] = useState<Array<CANCELLED_REQUEST.AsObject>>(
		[]
	);
	const [billStates, setBillStates] = useState<Array<LU_CANCELLED_BILL_STATE.AsObject>>(
		[]
	);
	const [state, setState] = useState<Istate>({
		listItemId: 0,
		selectListItem: false,
	});
	React.useEffect(() => {
		BillStates()
			.then((data) => {
				data.push({
					description: "",
				});
				setBillStates(data);
			})
			.catch((err) => {
				console.warn(err);
			});
	}, []);
	React.useEffect(() => {
		CancelledBillList(state.listItemId)
			.then((data) => {
				setCancelledBills(data.cancelledbilllistList);
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	}, [state.listItemId]);

	const commentTemplete = (formNo: number) => {
		return (
			<>
				<MasTooltip title="">
					<Link to="/cancellBillsAction">
						<IconButton aria-label="Edit" className={classes.circle}>
							<CreateRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
				<MasTooltip title="">
					<Link to={`/cancellBillsAction/${formNo}`}>
						<IconButton
							aria-label="delete"
							className={clsx(classes.circle, classes.circle2)}
							// onClick={() => handelCancelledBillRequest(formNo)}
						>
							<LinkRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
			</>
		);
	};
	const columns: IColumnProps[] = [
		{
			caption: "  ",
			width: 150,
			dataField: "formNo",
			alignment: "center",
		},
		{
			caption: " ",
			width: 160,
			dataField: "requestDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.requestDate);
				return date.toLocaleDateString();
			},
		},
		{caption: " ", dataField: "custkey", width: 160},
		{caption: " ", dataField: "surname"},
		{caption: "", dataField: "status"},
		{
			caption: "",
			dataField: "",
			cellRender: (row) => commentTemplete(row.data.formNo),
			type: "buttons",
		},
	];

	return (
		<Box className="page-wrapper">
			<MasPageHeader title="  " />
			<Grid spacing={2} container justify="center">
				<Grid item xs={3}>
					<Paper elevation={0} className={classes.list}>
						<MasList
							items={billStates}
							name="description"
							primaryItemText="description"
							textStyle={classes.listItemText}
							itemStyle={classes.listItem}
							handleListClick={(item) => {
								setState({
									listItemId: item.id,
								});
							}}
						/>
					</Paper>
				</Grid>
				<Grid item xs={9}>
					<Paper elevation={0} className={classes.table}>
						<Box className={classes.billActions}>
							<Grid container justify="space-between" alignItems="center">
								<Grid item>
									<Typography
										variant="h6"
										color="initial"
										className={classes.billActionsTitle}
									>
										   (3)
									</Typography>
								</Grid>
								<Grid item>
									<Link to="/cancellBillsRequest">
										<MasButton
											label="  "
											variant="contained"
											color="primary"
											disableElevation
											startIcon={<AddCircleOutline />}
										/>
									</Link>
									<Link to="/..">
										<MasButton
											label=""
											variant="outlined"
											color="primary"
											disableElevation
											startIcon={<PollRounded />}
										/>
									</Link>
								</Grid>
							</Grid>
						</Box>
						<Box className={classes.dataGrid}>
							<CancelBillsTable
								keyExpr="formNo"
								columns={columns}
								rows={cancelledBills}
								printPdf
							/>
						</Box>
					</Paper>
				</Grid>
			</Grid>
		</Box>
	);
}

const useStyles = makeStyles((theme) => ({
	list: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[3],
		boxShadow: theme.m_shadows[1],
	},
	listItem: {
		padding: "8px",
		backgroundColor: theme.palette.primary.light,
		"&:not(:last-child)": {
			marginBottom: theme.spacing(1),
		},
		"&.Mui-selected": {
			backgroundColor: theme.palette.primary.main,
			color: "#fff",
			"& $listItemText": {
				color: "#fff",
			},
			"&:hover": {
				backgroundColor: fade(theme.palette.primary.dark, 1),
			},
		},
		"&:hover": {
			backgroundColor: fade(theme.palette.primary.main, 0.25),
			"& $listItemText": {
				color: theme.palette.primary.main,
			},
		},
		borderRadius: "8px",
	},
	listItemText: {
		fontFamily: "DINMED",
		textAlign: "center",
		color: theme.palette.primary.dark,
		"& .MuiListItemText-primary": {
			fontSize: "1.1rem",
		},
	},
	table: {
		padding: theme.spacing(1.5, 2),
		borderRadius: theme.raduis[3],
		boxShadow: theme.m_shadows[1],
		height: "100%",
	},
	dataGrid: {
		border: theme.border[1],
		borderRadius: theme.raduis[1],
		padding: theme.spacing(1),
	},
	billActions: {
		marginBottom: theme.spacing(2),
	},
	billActionsTitle: {
		color: theme.palette.text.secondary,
	},
	circle: {
		width: 35,
		height: 35,
		borderRadius: "50%",
		backgroundColor: theme.palette.primary.main,
		color: theme.palette.secondary.main,
		margin: "0 4px",
		"&:hover": {
			backgroundColor: fade(theme.palette.primary.main, 0.7),
			boxShadow: theme.shadows[2],
		},
	},
	circle2: {
		backgroundColor: theme.palette.secondary.main,
		color: theme.palette.primary.main,
		"&:hover": {
			backgroundColor: fade(theme.palette.secondary.main, 0.7),
			boxShadow: theme.shadows[2],
		},
	},
}));
